<template>
    <div class="my-loan-info">
        <div class="field-part">
            <mt-cell title="贷款种类" :value="loanInfo.LOAN_TYPE"></mt-cell>
        </div>
        <div class="field-part">
            <mt-cell title="贷款金额" :value="loanInfo.LOAN_AMOUNT"></mt-cell>
            <mt-cell title="贷款余额" :value="loanInfo.LOAN_BALANCE"></mt-cell>
        </div>
        <div class="field-part">
            <mt-cell title="当期还款额" :value="loanInfo.CUR_REPAYMENT_AMOUNT"></mt-cell>
            <mt-cell title="借款日期" :value="loanInfo.LOAN_DATE"></mt-cell>
            <mt-cell title="每月还款日" :value="loanInfo.REPAYMENT_DATE"></mt-cell>
            <mt-cell title="已还期次" :value="loanInfo.REPAYMENT_PERIOD"></mt-cell>
        </div>
        <div class="field-part">
            <mt-cell title="贷款利率" :value="loanInfo.LOAN_RATE"></mt-cell>
        </div>

        <div class="button-bottom">
            <mt-button type="primary" size="large" @click="$router.go(-1)">返回</mt-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'my-loan-info',
        data() {
            return {
                loanInfo: null
            };
        },
        created() {
            let ls = this.$ls.getItem('loan-info');
            this.loanInfo = ls;
        },
        beforeRouteLeave(to, from, next) {
            this.$ls.removeItem('loan-info');
            next(true);
        }
    };

</script>

<style lang="scss" scoped>

.my-loan-info {
    
}

</style>


