<template>
    <div class="my-loan">
        <div class="hd">
            <div class="hd-title">
                <p>我的贷款情况</p>
            </div>
            <div class="hd-wrap hd-wrap-head">
                <div class="left">
                    <p class="balance" v-text="loan.LOAN_BALANCE"></p>
                    <p class="unit1">贷款余额（元）</p>
                </div>
                <div class="right">
                    <p class="total" v-text="loan.LOAN_AMOUNT"></p>
                    <p class="unit1">贷款总额（元）</p>
                </div>
            </div>
        </div>
        <!-- 贷款列表 -->
        <div class="cell-part main">
            <div v-if="loanList.length > 0">
                <div @click="getDetail(item)" class="loan-list" v-for="item in loanList">
                    <mt-cell :title="item.LOAN_TYPE" is-link></mt-cell>
                    <div class="hd-wrap">
                        <div class="left">
                            <p class="balance balance-color">{{item.LOAN_BALANCE}}</p>
                            <p class="unit2">贷款余额（元）</p>
                        </div>
                        <div class="right">
                            <p class="total">{{item.LOAN_AMOUNT}}</p>
                            <p class="unit2">贷款总额（元）</p>
                        </div>
                    </div>
                </div>
            </div>
            <div class="data-null" v-else>
                <div class="bg">
                    <div class="bg-pic"></div>
                    <p class="bg-msg">您还未在我行办理贷款</p>
                </div>
                <div class="button"></div>
            </div>
        </div>
    </div>
</template>

<script>
    import * as services from '@/services/my/my-data';
    export default {
        name: 'my-loan',
        data() {
            return {
                loanList: [],
                loan: {
                    LOAN_AMOUNT: '',
                    LOAN_BALANCE: ''
                },
                flag: 'false'
            };
        },
        created() {
            services.queryLoan()
                .then(res => {
                    if (res.data.length > 0) {
                        this.loan = res.data[0];
                        this.flag = true;
                    }
                }, err => {
                    console.log(err);
                });
            services.queryLoanList()
                .then(res => {
                    this.loanList = res.data;
                }, err => {
                    console.log(err);
                });
        },
        methods: {
            getDetail(item) {
                this.$ls.setItem('loan-info', item);
                this.$router.push({ name: 'MyLoanInfo' });
            }
        }
    };

</script>

<style lang="scss" scoped>
.my-loan{
    .hd {
        padding: 28px 50px;
        background-color: #777;
        color: #fff;
        @include image-set('../../../assets/img/my/loan-bg');
        background-size: cover;
        .hd-title p{
            font-size: 16px;
            color: #272727;
        }
    }

    .data-null {
        padding-top: 114px;
        text-align: center;
        font-size: 16px;
        .bg {
            text-align: center;
            .bg-pic {
                margin: auto;
                width: 177.8px;
                height: 243.8px;
                @include image-set('../../../assets/img/bank/transaction/no-record');
            }
            .bg-msg {
                margin-top: 40px;
                font-size: 17px;
                color: #666666;
            }
        }
        .button {
            position: fixed;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 220px;
            @include image-set('../../../assets/img/my/_bg');
        }
    }

    .loan-list {
        position: relative;
        background-color: #fff;
        margin-top: 40px;
        padding-bottom: 26px;
    }

    .hd-wrap {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        margin-top: 20px;

        .left {
            flex: 1;
            padding: 20px 0;
            text-align: center;

            p+p {
                margin-top: 12px;
            }

            .balance {
                font-size: 20px;
                color: #272727;
            }
            .balance-color {
                color: #ff942F;
            }
        }

        .right {
            position: relative;
            flex: 1;
            padding: 20px 0;
            text-align: center;
            @include mini-border(left, #ccc, before);

            p+p {
                margin-top: 12px;
            }

            .total {
                font-size: 20px;
                color: #272727;
            }
        }
        .unit1 {
            font-size: 13px;
            color: #666666;
        }
        .unit2 {
            font-size: 13px;
            color: #b5b5b5;
        }
    }
    .hd-wrap-head {
        margin-top: 70px;
        .right {
            @include mini-border(left, #e4e5e6, before);
        }
    }
    .mint-cell:after {
        left: 0;
    }
}
</style>


