<template>
    <div class="my-points-list">
        <template v-if="pointsList.length > 0">
            <div class="points-detail" v-for="item in pointsList">
                <div class="line1">
                    <span class="title">{{item.status}}</span>
                    <span class="money1" :class="{'money2': item.status !== '新增'}">{{item.amount}}</span>
                </div>
                <div class="line2">
                    <span class="from">积分来源</span>
                </div>
                <div class="line3">
                    <span class="time">{{item.date}}</span>
                    <p>
                        <span class="title">积分余额</span>
                        <span class="balance">{{item.balance}}</span>
                    </p>
                </div>
            </div>
        </template>
        <div class="none-my-points-list">
            <div class="bg">
                <div class="bg-pic"></div>
                <p class="bg-msg">暂无积分记录</p>
            </div>
            <div class="fixed-footer">
                <mt-button @click.native="$router.go(-1)" size="large" type="primary">返回</mt-button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'my-points-list',
        data() {
            return {
                pointsList: []
            };
        },
        created() {
            let ls = this.$ls.getItem('points-list');
            this.pointsList = ls;
            this.pointsList.filter(item => {
                if (item.status === 'in') {
                    item.status = '新增';
                    item.amount = '+' + item.amount;
                } else {
                    item.amount = '-' + item.amount;
                    item.status = '消费';
                }
            });
        },
        beforeRouteLeave(to, from, next) {
            this.$ls.removeItem('points-list');
            next(true);
        }
    };

</script>

<style lang="scss" scoped>
    .consume {
        color: #46c300;
    }

    .add {
        color: #ff942f;
    }
    .my-points-list {
        .points-detail {
            padding: 32px 50px 10px 50px;
            background-color: #fff;
            border-bottom: 1px solid #d9d9d9;
            > div {
                display: flex;
                justify-content: space-between;
                align-items: center;
            }
            .line1 {
                .title {
                    font-size: 16px;
                    color: #272727;
                }
                .money1 {
                    font-size: 20px;
                    color: #ff942f;
                }
                .money2 {
                    color: #46c300;
                }
            }
            .line2 {
                margin: 12px 0px 4px;
                font-size: 13px;
                color: #666666;
            }
            .line3 {
                font-size: 13px;
                color: #b5b5b5;
                p {
                    font-size: 13px;
                    .balance {
                        color: #2a2a2a;
                    }
                }
            }
        }
    }
    .none-my-points-list {
        padding-top: 260px;
        .bg {
            text-align: center;
            .bg-pic {
                margin: auto;
                width: 177.8px;
                height: 243.8px;
                @include image-set('../../../assets/img/bank/transaction/no-record');
            }
            .bg-msg {
                margin-top: 40px;
                font-size: 17px;
                color: #666666;
            }
        }
    }
</style>


