import ajax from '@/services/ajax';
import api from '@/services/api';

/*  查询我的积分总数 */
export function queryPoints(data) {
    return ajax.postAfterLoginQueryList('123456', data, api.queryPoints);
}
/*  查询我的积分列表 */
export function queryPointsList(data) {
    return ajax.postAfterLoginQueryList('123456', data, api.queryPointsList);
}
/*  查询我的贷款总数 */
export function queryLoan(data) {
    return ajax.postAfterLoginQueryList('123456', data, api.queryLoan);
}
/*  查询我的贷款列表 */
export function queryLoanList(data) {
    return ajax.postAfterLoginQueryList('123456', data, api.queryLoanList);
}



