<template>
    <div class="my-points">
        <div class="hd field-part">
            <div class="title">
                <p>我的积分</p>
                <img src="../../../assets/img/message.png" width="20" height="20">
            </div>
            <div class="value">{{points | formatNumber(2)}}</div>
            <div class="desc">
                此积分为客户综合积分总额
            </div>
        </div>
        <div class="field-part">
            <cell-date-zone title="时间跨度" zone="1M, 3M" ref="dateZone" @date-zone-change=" dateZoneChange "></cell-date-zone>
            <mt-cell title="起始日期" :value=" dataForm.beginDate " class="cell-text-info">
            </mt-cell>
            <mt-cell title="结束日期" :value=" dataForm.endDate " class="cell-text-info">
            </mt-cell>
        </div>

        <div class="field-hint">
            仅支持查询时间跨三个月内的交易明细记录
        </div>

        <div class="field-action">
            <mt-button type="primary" size="large" @click="query">查询</mt-button>
        </div>
    </div>
</template>

<script>
    import * as services from '@/services/my/my-data';
    export default {
        name: 'my-points',
        data() {
            return {
                points: '',
                dataForm: {
                    beginDate: '',
                    endDate: ''
                }
            };
        },
        created() {
            services.queryPoints({ showLoading: '加载中...' })
                .then(res => {
                    this.points = res.data[0].points;
                });
        },
        mounted() {
            this.$refs.dateZone.setDefault(this.dataForm.beginDate, this.dataForm.endDate);
        },
        methods: {
            dateZoneChange(begin, end) {
                this.dataForm.beginDate = begin;
                this.dataForm.endDate = end;
            },
            query() {
                let params = this.dataForm;
                services.queryPointsList(params)
                    .then(res => {
                        this.$ls.setItem('points-list', res.data);
                        this.$router.push({ name: 'MyPointsList' });
                    });
            }
        }
    };

</script>

<style lang="scss" scoped>
    .hd {
        position: relative;
        padding: 40px 0;
        text-align: center;
        @include image-set('../../../assets/img/my/points-bg');

        .title {
            p {
                display: inline-block;
                font-size: 13px;
                color: #999999;
            }
            img {
                display: inline-block;
            }
        }

        .value {
            margin: 20px 0;
            color: #444857;
            font-size: 30px;
        }

        .desc {
            font-size: 13px;
            color: #666666;
        }
    }
</style>




